
.players{margin-top:1em;display:-ms-flexbox;display:flex
}
.players .player1{border-right:1px solid #eee
}
.players .player1 .scores p{text-align:right;padding-right:0.75em
}
.players .player1 .player-name-input{text-align:right
}
.players .player2{border-left:1px solid #eee
}
.players .player2 .player-name{-ms-flex-order:0;order:0
}
.players .player2 .player-bummerl{-ms-flex-order:1;order:1
}
.players .player2 .scores p{text-align:left;padding-left:0.75em
}
.new{margin-top:0.5em;text-align:center
}
.slide-fade-enter-active{transition:all .1s ease
}
.slide-fade-leave-active{transition:all 0.3s cubic-bezier(1, 0.5, 0.8, 1)
}
.slide-fade-enter,.slide-fade-leave-active{padding-left:10px;opacity:0
}



